import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, onlyNumber, WindowComponent } from 'summer';
import { CLASS_NAME, PlanoSaude } from '../../../../models/dtos/mfp/planoSaude';

interface PlanoSaudeEditViewProps extends EditViewProps<PlanoSaude> {}

const PlanoSaudeEditView: FC<PlanoSaudeEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);

  const formProps = useForm<PlanoSaude>({ defaultValues: selectedItem, mode: 'onBlur' });

  return (
    <WindowComponent title="Manutenção de Plano de Saúde" wikiId={2338}>
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: onlyNumber(0, 8) }}
              id="codigo"
              isPrependLabel={true}
              label="Código"
              labelWidth={80}
              placeholder="Informe o Código"
              type={InputType.NUMBER}
              tooltipProps={{ message: 'Preenchimento Opcional - Codificação Automática', placement: 'bottom' }}
            />
          </Col>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { maxLength: 250, required: true } }}
              id="razaoSocial"
              inputProps={{ autoFocus: true }}
              isPrependLabel={true}
              label="Razão Social"
              labelWidth={120}
              placeholder="Informe a Razão Social"
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="cnpj"
              isPrependLabel={true}
              label="CNPJ"
              labelWidth={80}
              placeholder="Informe o CNPJ"
              type={InputType.CNPJ}
            />
          </Col>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { ...onlyNumber(0, 6), required: true } }}
              id="registroANS"
              isPrependLabel={true}
              label="Registro ANS"
              labelWidth={120}
              placeholder="Informe o Registro da ANS"
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default PlanoSaudeEditView;
